import React, { useRef } from "react";
import { useDispatch } from "react-redux";
import clsx from "clsx";
import * as Yup from "yup";
import PropTypes from "prop-types";
import { Formik } from "formik";
import {
  Box,
  Button,
  Checkbox,
  FormHelperText,
  TextField,
  Typography,
  Link,
  makeStyles,
  InputAdornment,
  IconButton,
  Grid,
  ListItemText,
  Menu,
  MenuItem,
  FormControlLabel,
  Paper,
  Card,
  CardContent,
  CardHeader,
  Divider,
} from "@material-ui/core";
import { register } from "src/actions/accountActions";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import { useState } from "react";
import DraftEditor from "src/components/DraftEditor";
import { EditorState, convertToRaw } from "draft-js";
import draftToHtml from "draftjs-to-html";
import { createProductTypeRequest } from "src/actions/productTypeActions";
import ImageDropzone from "src/components/ImageDropzone";

const useStyles = makeStyles(() => ({
  root: {},
}));

function CreateForm({ className, onSubmitSuccess, onSubmitFailed, ...rest }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [showPassword, setShowPassword] = useState(false);
  const [isOpen, setIsOpen] = useState(true);
  const [body, setBody] = useState("");
  const [thumbnail, setThumbnail] = useState("");

  return (
    <Formik
      initialValues={{
        nameVi: "",
        nameEn: "",
      }}
      validationSchema={Yup.object().shape({
        nameVi: Yup.string()
          .max(255)
          .required("Thng tin bt buc"),
        nameEn: Yup.string()
          .max(255)
          .required("Thng tin bt buc"),
      })}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          var objet = {
            nameVi: values.nameVi,
            nameEn: values.nameEn,
          };
          await dispatch(
            createProductTypeRequest(
              objet,
              (res) => {
                onSubmitSuccess();
              },
              (error) => {
                console.log("errorerror", error);
                onSubmitFailed();
              }
            )
          );
        } catch (error) {
          setStatus({ success: false });
          setErrors({ submit: error.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values,
      }) => (
        <form
          className={clsx(classes.root, className)}
          onSubmit={handleSubmit}
          {...rest}
        >
          <Grid container justify="space-between" spacing={3}>
            <Grid item xs={12} lg={6}>
              <TextField
                error={Boolean(touched.nameVi && errors.nameVi)}
                fullWidth
                helperText={touched.nameVi && errors.nameVi}
                label="Tn loi sn phm (Vi)"
                margin="normal"
                name="nameVi"
                onBlur={handleBlur}
                onChange={handleChange}
                type="text"
                value={values.nanameVime}
                variant="outlined"
              />
              <TextField
                error={Boolean(touched.nameEn && errors.nameEn)}
                fullWidth
                helperText={touched.nameEn && errors.nameEn}
                label="Tn loi sn phm (En)"
                margin="normal"
                name="nameEn"
                onBlur={handleBlur}
                onChange={handleChange}
                type="text"
                value={values.nameEn}
                variant="outlined"
              />
              <Box mt={2}>
                <Button
                  color="secondary"
                  disabled={isSubmitting}
                  fullWidth
                  size="large"
                  type="submit"
                  variant="contained"
                >
                  Thm mi
                </Button>
              </Box>
            </Grid>
            <Grid item xs={12} lg={6}></Grid>
          </Grid>
        </form>
      )}
    </Formik>
  );
}

CreateForm.propTypes = {
  className: PropTypes.string,
  onSubmitSuccess: PropTypes.func,
};

CreateForm.default = {
  onSubmitSuccess: () => {},
};

export default CreateForm;
