import React from "react";
import { Container, makeStyles } from "@material-ui/core";
import Page from "src/components/Page";
import Header from "./Header";
import CreateForm from "./CreateForm";
import { useHistory } from "react-router";
import { useSnackbar } from "notistack";
const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: "100%",
    paddingTop: theme.spacing(3),
    paddingBottom: 100,
  },
}));

function JobCreateView() {
  const classes = useStyles();
  const history = useHistory();
  const { enqueueSnackbar } = useSnackbar();

  const handleSubmitSuccess = () => {
    enqueueSnackbar("Thm mi thnh cng", {
      variant: "success",
    });
    history.push("/app/management/productTypes");
  };
  const handleSubmitFailed = () => {
    console.log("handleSubmitFailed");
    enqueueSnackbar("Thm mi tht bi", {
      variant: "error",
    });
  };
  return (
    <Page className={classes.root} title="User Create">
      <Container maxWidth="lg">
        <Header />
        <CreateForm
          onSubmitSuccess={handleSubmitSuccess}
          onSubmitFailed={handleSubmitFailed}
        />
      </Container>
    </Page>
  );
}

export default JobCreateView;
